import React, { Component } from 'react'

export default class DuaWrapper extends Component {
    state = {
        duas: [
            {
                id:undefined,
                title: undefined,
                body: undefined,
                category: undefined,
                source: undefined,
                chain: undefined
            }
        ]
    }

    onHandleChange(e) {
        console.log(e);
    }

    render() {
        const duas = this.state.duas;
        return (
            <div>
                <FormComponent/>
            </div>
        )
    }
}

const FormComponent = (props) => {
    return (
        <form onChange={this.onHandleChange}>
            <input type="text" name="title"/>
        </form>
    );
};





